* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #200042;
    color:#fff;
    font-family: Calibri;
}

.header {
    background-color: #4e2d9b;
    padding: 20px;
    border-radius: 20px;
}
.headerBlock {
    text-align: center;
}
.headerButton {
    border-radius: 15px;
    background-color: #5b22cd;
    padding: 10px 20px;
    margin: 20px;
    color:#fff;
    text-align: center;
    cursor: pointer;
    text-decoration: none;
}
.headerButton:hover {
    border-color: silver;
    border-width: 10px;
    border-radius: 15px;
    background-color: #321c64;
    padding: 10px 20px;
    margin: 20px;
    cursor: pointer;
}
.login {
    border-color: blue;
    border-width: 10px;
    text-align: center;
    margin-top: 15%;
    width: fit-content;
    background: #4e2d9b;
    padding: 50px;
}
.reg {
    border-color: blue;
    border-width: 10px;
    text-align: center;
    margin-top: 8%;
    width: fit-content;
    background: #4e2d9b;
    padding: 50px;
}
.loginInput {
    width: 250px;
    height: 40px;
    text-align: center;
    color: white;
    border-width: 2px;
    border-color: black;
    border-radius: 15px;
    font-family: Calibri;
    font-size: larger;
    cursor: pointer;
    background: #5b22cd;
    text-decoration: none;
    padding: 5px;
    margin: 3px;
}
.loginInput:hover {
    background: #4e2d9b;
}
h1 {
    text-align: center;
    font-family: Calibri;
}
h3 {
    text-align: center;
    font-family: Calibri;
}
.loginBox {
    width:100%;
    display: flex;
    justify-content: center;
}
::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color: white;
}
.chatItem {
    text-align: center;
    padding: 10px;
    width: 100%;
    height: min-content;
    cursor: pointer;
    background: #5b22cd;
    color: white;
    font-family: Calibri;
    font-size: larger;
    margin-bottom: 10px;
    border-width: 5px;
    border-color: black;
    border-radius: 20px;
}
.chatItem:hover {
    background: #4e2d9b;
}
.chatsTable {
    margin: 5%;
    border-color: #4e2d9b;
    border-width: 5px;
}
.chatsTableBlock {
    float: left;
    display: block;
    width: fit-content;
    overflow-Y: auto;
    overflow-x: hidden;
    height: 190mm;
    margin-top: 5%;
}
.chatBlock {
    float: left;
    display: block;
    width: 65%;
    margin: 5%;
    height: 190mm;
}
.scrollingChat {
    width: 100%;
    height: 600px;
    overflow-y: auto;
    overflow-x: auto;
}
.inputInChat {
    width: 80%;
    padding-top: 2px;
    padding-bottom: 2px;
    margin-left: 2%;
    font-size: large;
    font-family: Calibri;
    background: #5b22cd;
    color: white;
    border-radius: 15px;
    text-align: center;
}
.inputInChat:hover {
    background: #4e2d9b;
}
.buttonInChat {
    width: 7%;
    height: min-content;
    margin-left: 1%;
    padding-top: 5px;
    font-family: Calibri;
    font-size: large;
    background: #5b22cd;
    color: white;
    border-radius: 15px;
    cursor: pointer;
}
.buttonInChat:hover {
    background-color: #4e2d9b;
}
.MessageChat {
    background: rgba(122, 0, 252, 0.3);
    padding: 5px;
    width: fit-content;
    border-radius: 15px;
    margin: 1%;
}
.UserNameInChat {
    padding: 10px;
    margin-top: 5px;
}
.MessageInChat {
    padding: 5px;
    width: 10%;
    word-break: break-word;
}
.MessageItem {
    margin-top: 2px;
    margin-bottom: 2px;
}
.MediaInChat {
    margin-left: 10px;
}
.Sticker {
    width: 150px;
    height: 150px;
}
.SmallSticker {
    width: 80px;
    height: 80px;
    cursor: pointer;
}
.Image {
    width: 300px;
}
.chatTable {
    text-align: center;
}
.loadChatButton {
    text-align: end;
}
.TextMessageDiv {
    text-align: start;
    font-family: Calibri;
    font-size: medium;
}
.Stickers {
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 8px;
    background: #4e2d9b;
    width: 100%;
    height: 100px;
    overflow-y: auto;
}
body::-webkit-scrollbar {
    width: 10px;
}
body::-webkit-scrollbar-track {
    background: #321c64;
    border-radius: 20px;
}
body::-webkit-scrollbar-thumb {
    background-color: #5b22cd;
    border-radius: 20px;
}
.scrollingChat::-webkit-scrollbar {
    width: 10px;
}
.scrollingChat::-webkit-scrollbar-track {
    background: #321c64;
    border-radius: 20px;
}
.scrollingChat::-webkit-scrollbar-thumb {
    background-color: #5b22cd;
    border-radius: 20px;
}
.chatsTableBlock::-webkit-scrollbar {
    width: 10px;
}
.chatsTableBlock::-webkit-scrollbar-track {
    background: #321c64;
    border-radius: 20px;
}
.chatsTableBlock::-webkit-scrollbar-thumb {
    background-color: #5b22cd;
    border-radius: 20px;
}
.Stickers::-webkit-scrollbar {
    width: 10px;
}
.Stickers::-webkit-scrollbar-track {
    background: #321c64;
    border-radius: 20px;
}
.Stickers::-webkit-scrollbar-thumb {
    background-color: #5b22cd;
    border-radius: 20px;
}
.ChatPanel {
    background-color: #321c64;
    border-radius: 15px;
    padding: 10px;
}
#Send {
    float: right;
}
.MsgTime {
    font-size: smaller;
    text-align: end;
    padding-bottom: 0px;
}
.WithBG {
    background: rgba(122, 122, 122, 1);
    border-radius: 10px;
    padding: 5px;
}
.WithoutBG {
    background: transparent;
}
.ReplyBtn {
    background-color: #5b22cd;
    border-radius: 5px;
    float: right;
    font-size: x-small;
    cursor: pointer;
    width: max-content;
    height: max-content;
}
.Reply {
    background-color: #321c64;
    width: 50%;
    text-align: center;
    border-radius: 15px;
}